/* Based on the work of Chris Rickels (https://codepen.io/cRckls/pen/DJvZXo), but quite modified by me */

.crt {
  overflow: hidden;
  position: relative;
}

.crt::before {
    content: " ";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: linear-gradient(
        to bottom,
        rgba(255,255,255,0),
        rgba(255,255,255,0) 50%,
        rgba(0,0,0,0.2) 70%,
        rgba(0,0,0,0.6)
    );
    z-index: 5;
    background-size: 100% 3px, 4px 100%;
    pointer-events: none;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes blur {
  0% {
    filter: blur(0.8px);
    opacity: 0.8;
  }
  50% {
    filter: blur(0.8px);
    opacity: 1;
  }
  100% {
    filter: blur(0.8px);
    opacity: 0.8;
  }
}

@keyframes jerk {
  50% {
    transform: translateX(1px);
  }
  51% {
    transform: translateX(0);
  }
}

@keyframes jerkup {
  50% {
    transform: translateY(1px);
  }
  51% {
    transform: translateY(0);
  }
}

@keyframes jerkblue {
  0%,
  30% {
    transform: translateX(0);
  }
  31% {
    transform: translateX(5px);
  }
  32%,
  98% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(5px);
  }
}

@keyframes jerkgreen {
  0%,
  30% {
    transform: translateX(0);
  }
  31% {
    transform: translateX(-5px);
  }
  32%,
  98% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-5px);
  }
}

@keyframes jerkwhole {
  30% {
  }
  40% {
    opacity: 1;
    top: 0;
    left: 0;
    transform: scale(1, 1);
    transform: skew(0, 0);
  }
  41% {
    opacity: 0.8;
    top: 0px;
    left: -100px;
    transform: scale(1, 1.2);
    transform: skew(50deg, 0);
  }
  42% {
    opacity: 0.8;
    top: 0px;
    left: 100px;
    transform: scale(1, 1.2);
    transform: skew(-80deg, 0);
  }
  43% {
    opacity: 1;
    top: 0;
    left: 0;
    transform: scale(1, 1);
    transform: skew(0, 0);
  }
  65% {
  }
}
